
import InfiniteLoading from 'vue-infinite-loading';
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
components: {
Tab,
TabItem,
InfiniteLoading
},
data () {
return {
bottomCount: 0,
type: 'a1',
types: ['a1', 'a2', 'a3'],
a1: {
page: 1,
data: []
},
a2: {
page: 1,
data: []
},
a3: {
page: 1,
data: []
}
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
getList ($state) {




















setTimeout(() => {
this.bottomCount += 10
if (this.bottomCount < 50) {
$state.loaded();
} else {
$state.complete();
}
}, 1000)
},
showRefuseConfirm(){
this.$vux.confirm.show({

showInput: true,
title: '请填写拒绝原因,反馈给申请人',
placeholder: '限15字以内',
inputAttrs: {
type: 'textarea'
},
onCancel () {
console.log(this) 
console.log(_this) 
},
onConfirm () {
}
})
},
cancelConfirm(){
this.$vux.confirm.show({

showInput: true,
title: '是否确认取消其代理资格？',
placeholder: '（选填）可在此填写取消资格原因',
inputAttrs: {
type: 'textarea'
},
onCancel () {
console.log(this) 
console.log(_this) 
},
onConfirm () {
}
})
}
},
mounted() {

}
}



